import React from 'react';
import './Arrows.css'

function Arrows(props) {
	return (
		<div className='arrows'>
			<div className="arrow arrow-1"></div>
			<div className="arrow arrow-2"></div>
			<div className="arrow arrow-3"></div>
		</div>
	)
}

export default Arrows;


